<script>
export default {
    data(){
        return{
            activeName: 'first',
            tabPosition: 'left',
            result:'',
            dialogFormVisible: false,
            dialogForm: false,
            formLabelWidth: '120px',
            dateTime:'',
            form:{
                search:null,
                select:null,
            },
            form1:{
                select_zb:null,
                select_zy:null,
            },
            zhuanye:[{label:'汽修',value:'汽修'},{label:'计算机',value:'计算机'},{label:'法律',value:'法律'},{label:'金融',value:'金融'},{label:'建筑',value:'建筑'}],
            zhibiao:[{label:'在校学生数',value:'在校学生数'},{label:'“双师型”教师数量',value:'“双师型”教师数量'},{label:'毕业生数量',value:'毕业生数量'},{label:'兼职老师数量',value:'兼职老师数量'},{label:'学校课程数',value:'学校课程数'},{label:'学校专业数',value:'学校专业数'},{label:'就业率',value:'就业率'},{label:'招生人数',value:'招生人数'},],
            peoples:[{label:'王二',value:'王二'},{label:'张三',value:'张三'},{label:'李四',value:'李四'},{label:'赵五',value:'赵五'},],
            num:null,
            name1:[
                {name:'张三',value:'1'},
                {name:'张三',value:'2'},
                {name:'张三',value:'3'},
                {name:'张三',value:'4'},
                {name:'张三',value:'5'},
                {name:'张三',value:'6'},
                {name:'张三',value:'7'},
                {name:'张三',value:'8'},
                {name:'张三',value:'9'},
                {name:'张三',value:'10'},
                {name:'张三',value:'11'},
                {name:'张三',value:'12'},
                {name:'张三',value:'13'},
                {name:'张三',value:'14'},
                {name:'张三',value:'15'},
                {name:'张三',value:'16'},
                {name:'张三',value:'17'},
            ],
            content:[{
                yijian1:'',
                cuoshi1:'',
                xiaoguo1:'',
            },{
                yijian2:'',
                cuoshi2:'',
                xiaoguo2:'',
            },{
                yijian3:'',
                cuoshi3:'',
                xiaoguo3:'',
            },{
                yijian4:'',
                cuoshi4:'',
                xiaoguo4:'',
            },{
                yijian5:'',
                cuoshi5:'',
                xiaoguo5:'',
            },{
                yijian6:'',
                cuoshi6:'',
                xiaoguo6:'',
            },{
                yijian7:'',
                cuoshi7:'',
                xiaoguo7:'',
            },{
                yijian8:'',
                cuoshi8:'',
                xiaoguo8:'',
            },{
                yijian9:'',
                cuoshi9:'',
                xiaoguo9:'',
            },{
                yijian10:'',
                cuoshi10:'',
                xiaoguo10:'',
            },{
                yijian11:'',
                cuoshi11:'',
                xiaoguo11:'',
            },{
                yijian12:'',
                cuoshi12:'',
                xiaoguo12:'',
            },{
                yijian13:'',
                cuoshi13:'',
                xiaoguo13:'',
            },{
                yijian14:'',
                cuoshi14:'',
                xiaoguo14:'',
            },{
                yijian15:'',
                cuoshi15:'',
                xiaoguo15:'',
            },{
                yijian16:'',
                cuoshi16:'',
                xiaoguo16:'',
            },{
                jielun: '',
                jianyi: ''
            }]
        }
    },
    methods:{
        handleClick(tab, event) {
            console.log(tab, event);
        },
        querySearch(queryString, cb) {
            var restaurants = this.restaurants;
            var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
            // 调用 callback 返回建议列表的数据
            cb(results);
        },
        createFilter(queryString) {
            return (restaurant) => {
                return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
            };
        },
        loadAll() {
            return [
                { "value": "王二", "name": "王二" },
                { "value": "张三", "name": "张三" },
                { "value": "李四", "name": "李四" },
                { "value": "赵五", "name": "赵五" },
            ];
        },
        handleSelect(item) {
            console.log(item);
        },
        fenpei(name){
            console.log(name)
            this.form.search = name.name
            this.dialogFormVisible = true
            this.num = name.value - 1
        },
        confirm(){
            this.dialogFormVisible = false
            console.log(this.form.search)
            this.name1[this.num].name = this.form.search
            console.log(this.name1[this.num])
            this.form.search = ""
        },
        searchData(){
            this.dialogForm = true
        },
        jiansuo(){
            // var Result = document.getElementById('result')
            // Result.style.display = 'block'
            if(this.form1.select_zb && this.form1.select_zy){
                this.result = '152'
            }else{
                this.result = '无结果'
            }
            
        },
        close(){
            var Result = document.getElementById('result')
            this.dialogForm = false
            this.form1.select_zb = null
            this.form1.select_zy = null
            this.form1.search = null
            this.zhibiao.select = null
            this.result = null
        }

    },
    mounted() {
        this.restaurants = this.loadAll();
    },

}
</script>

<style>
#zwzg{
    width: 100%;
    height: 100%;
}
#zwzg .title{
    width: 100%;
    height: 50px;
    line-height: 50px;
    margin: 40px 0 10px;
    font-size: 24px;
    font-weight: 900;
    color: black;
    text-align: center
}
#zwzg .tb-edit .el-input {
    display: none
}
#zwzg .tb-edit .current-row .el-input {
    display: block
}
#zwzg .tb-edit .current-row .el-input+span {
    display: none
}
#zwzg .is-active{
    color: #029348 !important;
}
#zwzg .el-tabs__active-bar{
    background: #029348 !important;
}
#zwzg .is-top :hover{
    color: #029348 !important;
}
#zwzg .is-left :hover{
    color: #029348 !important;
}
#zwzg .el-card{
    padding: 40px 0 40px 0;
}
#zwzg .el-tabs--left .el-card{
    padding: 60px 0 40px 0;
}
#zwzg .task{
    width: 80%;
    margin: 0 auto;
    margin-bottom: 10px;
    display: flex;
}
#zwzg .task .fenpei{
    width: 200px;
    float: right;
}
#zwzg .task .fenpei .name{
    font-size: 14px;
}
#zwzg .task .s_btn{
    font-size: 14px !important;
}
#zwzg .task .search{
    width: 100px;
    float: right;
}
#zwzg .table{
    width: 80%;
    margin: 0 auto;
    border: 1px solid #e2e1e1;
}
#zwzg .table .zdyj{
    border-bottom: 1px solid #e2e1e1;
    position: relative;
}
#zwzg .table .gjcs{
    border-bottom: 1px solid #e2e1e1;
    position: relative;
}
#zwzg .table .gjxg{
    position: relative;
}
#zwzg .table .tit{
    height: 60px;
    line-height: 60px;
    padding-left: 20px;
    font-weight: 900;
    border-bottom: 1px solid #e2e1e1;
}
#zwzg .tabletitle{
    width: 80%;
    margin: 0 auto;
    margin-top: 20px;
    margin-bottom: 20px;
    font-weight: 900;
}
#zwzg .table .input_box{
    flex: 1;
    height: 190px;
    margin: 10px;
    margin-left: 20px;
}
#zwzg .table .input_box .text_box{
    width: 100%;
    height: 100%;
    resize: none;
    border: 0;
    padding: 0;
    outline: none;
}
#zwzg .table .count{
    position: absolute;
    right: 5px;
    top: 40px;
    font-size: 14px;
    color: #aaaaaa; 
}
#zwzg .tiaojian{
    font-size: 18px;
    margin-left: 90px;
    margin-bottom: 20px;
}
#zwzg #result{
    padding-left: 90px;
}
#zwzg .dialog-footer{
    display: flex;
    margin-left: 90px;
}
#zwzg .el-dialog__header .el-dialog__title{
    margin-left: 90px;
}
</style>



